body {
	position: relative;
	min-height: 100vh;
	background-color: #454343;
	margin: 0;
}


img {
	max-width: 200px;
}

.full {
	display: flex;
	justify-content: center;
	width: 100%;
    height: 70vh;
    margin-left: auto;
}

@media (min-width: 960px) {
	.full {
		position: fixed;
		height: 100vh;	

		/* max-width: 1400px; */
		width: 100%;

		padding-right: 40px;
		padding-left: 150px;
		top: 0;
		right: 0;
		bottom: 0;
		max-width: calc(100% - 100px - 350px);
	}
}

#full-img {
	max-width: 100%;
	width: 100%;
	height: 100%;
	background-position: center;
	background-size: 100% auto;
	background-repeat: no-repeat;
}

@media (min-width: 960px) {
	#full-img {
		max-width: 100%;
		width: 100%;
		margin-top: 0;
		margin-bottom: 0;
	}
}

.list {
	display: flex;
	flex-wrap: nowrap;
	height: 12vh;

	margin-left: 10px;
    margin-top: 10px;
	margin-bottom: 10px;
    
}

@media (min-width: 960px) {
	.list {
		display: flex;
		flex-direction: column;
		margin-left: 20px;
		top: 0;
		bottom: 0;
	}
}

.list img {
	padding-left: 10px;
	padding-top: 10px;
	width: 120px;
	opacity: .5;
	/* transition: .2s; */
}

@media (min-width: 960px) {
	.list img{
		width: 255px;	
	}
}

.list img:last-child {
	padding-right: 20px;
}

@media (min-width: 960px) {
	.list img:last-child {
		padding-right: 0;
	}
}

.list img:focus {
	outline: none;
 }


.list img.active,
.list img:focus {
	opacity: 1;
}
@media (min-width: 960px) {
	.list img:hover,
	.list img.active,
	.list img:focus {
		cursor: pointer;
		box-shadow: 17px 20px 16px 0px rgba(0, 0, 0, 0.35);
		opacity: 1;
		filter: grayscale(0);
	 }
}

@media (min-width: 960px) {
	.list img{
		max-width: 100%;
		max-height: none;
		margin-bottom: 15px;
		filter: grayscale(100%);
		opacity: .5; 
		
		transition: .2s;

     	margin-right: 20px;
 	}
}


.list-wrap {
    
	overflow: scroll;
}

@media (min-width: 960px) {
	.list-wrap {
		position: absolute;
		overflow: inherit;
		margin: 0;
		bottom: 0;
		top: 0;
		
	}
}


.left,
.right {
	width: 20px;
	padding: 0;
	border: none;
	font: inherit;
	color: inherit;
	background-color: transparent;
	outline: none;
}

@media (min-width: 960px) {
	.left:hover,
	.right:hover {
		cursor: pointer;
		transform: scale(1.5); 
	}
}

@media (min-width: 960px) {
	.left {
		width: 60px;
		padding-right: 20px;
		transition: .2s;
	}
}

@media (min-width: 960px) {
	.right {
		width: 60px;
		padding-left: 20px;
		transition: .2s;
    }
}